import { useState } from "react";
import { useDispatch } from "react-redux";
import { get_emotion_prediction } from "../../redux/actions/rl";
import { useNavigate } from "react-router-dom";

function FormAI() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [respuestas, setRespuestas] = useState(Array(15).fill(1));
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const handleChange = (index, value) => {
        const nuevasRespuestas = [...respuestas];
        nuevasRespuestas[index] = parseInt(value);
        setRespuestas(nuevasRespuestas);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        setError(null);

        try {
            const action = await dispatch(get_emotion_prediction(respuestas));
            if (action.type === "EMOTION_PREDICTION_SUCCESS") {
                navigate("/resultado-emocion", { state: { resultado: action.payload } });
            } else {
                setError("No se pudo obtener la prediccin.");
            }
        } catch (err) {
            console.error("Error en prediccin:", err);
            setError("Hubo un error al procesar tus respuestas.");
        }

        setLoading(false);
    };

    const preguntas = [
        // Emociones positivas
        "Me siento feliz y satisfecho/a con mi vida en este momento.",
        "Tengo la energa suficiente para realizar mis actividades diarias.",
        "Me siento optimista respecto a mi futuro.",
        "Disfruto de las actividades que realizo diariamente.",
        "Me siento valorado/a por las personas cercanas a m.",
        // Emociones negativas
        "Me siento estresado/a o abrumado/a con frecuencia.",
        "Me cuesta concentrarme en mis actividades diarias.",
        "Me siento desanimado/a con frecuencia.",
        "Tengo dificultades para manejar mis emociones.",
        "Me siento solo/a con frecuencia.",
        // Ansiedad y preocupacin
        "Me preocupo en exceso por problemas o situaciones.",
        "Tengo dificultades para dormir debido a preocupaciones.",
        "Me siento ansioso/a incluso sin una razn clara.",
        "Me siento constantemente en alerta o tenso/a.",
        "Experimento sntomas fsicos como palpitaciones o sudoracin."
    ];

    return (
        <div className="min-h-screen flex items-center justify-center px-6 py-12 bg-gray-900 text-gray-200 font-sans">
            <div className="max-w-xl w-full bg-gray-800 shadow-2xl rounded-3xl p-8">
                <h1 className="text-3xl font-bold text-center text-blue-400 mb-6">
                     Prediccin de tu Estado Emocional
                </h1>

                {error && (
                    <div className="mb-4 text-red-400 text-sm text-center">
                        {error}
                    </div>
                )}

                <form onSubmit={handleSubmit} className="space-y-6 max-h-[600px] overflow-y-auto">
                    {preguntas.map((texto, index) => (
                        <div key={index} className="mt-4">
                            <label className="block font-medium text-gray-300">
                                {texto}
                            </label>
                            <input
                                type="number"
                                min="1"
                                max="5"
                                required
                                value={respuestas[index]}
                                onChange={(e) => handleChange(index, e.target.value)}
                                className="mt-2 w-full border border-gray-600 rounded-lg bg-gray-900 px-4 py-2 text-gray-300"
                            />
                        </div>
                    ))}

                    <button
                        type="submit"
                        disabled={loading}
                        className="w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 rounded-lg shadow-md"
                    >
                        {loading ? "Procesando..." : "Enviar respuestas"}
                    </button>
                </form>
            </div>
        </div>
    );
}

export default FormAI;
